.sig-up-dialog  {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgba(19, 41, 75, 0.6)), to(rgba(19, 41, 75, 0.6))), url("/images/photo.jpg");
  background-image: -webkit-linear-gradient(bottom, rgba(19, 41, 75, 0.6), rgba(19, 41, 75, 0.6)), url("/images/photo.jpg");
  background-image: -o-linear-gradient(bottom, rgba(19, 41, 75, 0.6), rgba(19, 41, 75, 0.6)), url("/images/photo.jpg");
  background-image: linear-gradient(to top, rgba(19, 41, 75, 0.6), rgba(19, 41, 75, 0.6)), url("/images/photo.jpg");
  background-position: center;
  background-size: cover;

}

.log-in-page__inputs-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 5px 0 5px;
}

.log-in-page__inputs-container input {
  outline: none;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: bold;
  margin: 5px 5px 10px 5px;
  width: 360px;
}

.log-in-page__line {
  width: 400px;
  height: 1px;
  min-height: 1px;
  background-color: white;
}

.log-in-page__icon{
  color: white;
  background-color: rgba(0, 0, 0, 0);
  margin-right: 15px;
}

.log-in-page__elements-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 25px;
}

.sign-up-button{
  background-color: rgb(232,62,83);
  border-radius: 25px;
  color: white;
  border-style: solid;
  border-color: rgb(232,62,83);
  font-family: 'Montserrat', sans-serif;
  font-size: 17px;
  font-weight: bold;
  width: 300px;
  height: 45px;
  cursor: pointer;
  margin: 30px 0 5px 0;
}

button:focus {outline:0;}


